<template>
    <nav>
        <v-app-bar app flat color="#404242" class="white--text">

            <v-app-bar-nav-icon color="white" @click.stop="drawer = !drawer"/>
            <v-toolbar-title>
                <span v-if="!drawer">M S A</span>
            </v-toolbar-title>

            <v-spacer/>

            <v-menu offset-y>
                <template v-slot:activator="{ on }">
                    <v-icon v-on="on" color="white">mdi-apps</v-icon>
                </template>

                <v-list>
                    <div v-for="link in links" :key="link.name">

                        <v-list-item v-if="selectMenuList(link.name)"
                                     router :to="link.route">
                            <v-list-item-title>{{link.name}}</v-list-item-title>
                        </v-list-item>
                    </div>

                    <v-list-item v-if="isAuthenticated"
                                 @click="LOGOUT">
                        <v-list-item-title>로그아웃</v-list-item-title>
                    </v-list-item>
                </v-list>

            </v-menu>
        </v-app-bar>

        <!-- SECTION: 옆쪽 Drawer
        ====================================================================== -->
        <v-navigation-drawer app v-model="drawer" dark>
            <v-col class="mt-5 text-center">
                <h2 class="white--text my-3">
                    M S A
                </h2>
            </v-col>
            <hr class="ma-3 white my-hr"/>
            <v-list>
                <div v-for="link in links" :key="link.text">
                    <v-list-item v-if="link.route != null" router :to="link.route">
                        <v-list-item-action>
                            <v-icon color="white">{{link.icon}}</v-icon>
                        </v-list-item-action>
                        <v-list-item-content class="white--text">
                            {{link.name}}
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item v-else :href="link.href" target="_blank" class="cyan lighten-2">
                        <v-list-item-action>
                            <v-icon color="white">{{link.icon}}</v-icon>
                        </v-list-item-action>
                        <v-list-item-content class="white--text">
                            {{link.name}}
                        </v-list-item-content>
                    </v-list-item>
                </div>
            </v-list>
        </v-navigation-drawer>
        <!-- SECTION: 옆쪽 Drawer -->
    </nav>
</template>

<script>
    import {mapGetters, mapMutations} from 'vuex'

    export default {
        name: "AppBar",
        data() {
            return {
                drawer: true,
                links: [
                    {name: '메인', icon: 'mdi-home', route: '/'},
                    {name: '메모장', icon: 'mdi-note-text', route: '/memo'},
                    {name: '게시글', icon: 'mdi-clipboard-text-multiple-outline', route: '/boards'},
                    {name: '로그인', icon: 'mdi-login-variant', route: '/login'},
                    {name: '회원가입', icon: 'mdi-account-plus', route: '/register'},
                ]
            }
        },
        computed: {
            ...mapGetters(['isAuthenticated'])
        },
        methods: {
            ...mapMutations(['LOGOUT']),
            selectMenuList(name) {
                if (name === '로그인' || name === '회원가입') {
                    if (this.isAuthenticated) {
                        return false;
                    }
                }

                return true;
            }
        }


    }
</script>

<style scoped>
    a {
        text-decoration: none;
    }
</style>